/* Координаты курсора для эффекта подсвечивания бордера (задаются скриптом) */
.product-card {
  --mouse-x: 50%;
  --mouse-y: 50%;
}

.product-card {
  background: #252525;
  border-radius: 20px;
  padding: 30px;
  padding-bottom: 80px;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: visible;
  box-sizing: border-box;
}

/* Обводка — только радиальный градиент от курсора в кольце 2px (центр карточки без градиента) */
.product-card::before {
  content: "";
  position: absolute;
  left: -2px;
  top: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  padding: 2px;
  box-sizing: border-box;
  background: radial-gradient(
    550px circle at var(--mouse-x) var(--mouse-y),
    #59C2FF,
    transparent 50%
  );
  border-radius: 22px;
  z-index: 0;
  pointer-events: none;
  transition: opacity 500ms;
  /* Маска: градиент виден только в обводке 2px, центр вырезан */
  -webkit-mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  mask-composite: exclude;
}

.product-card:hover::before {
  opacity: 1;
}

@media (max-width: 1200px) {
  .product-card::before,
  .achievement-card::before,
  .task-card::before,
  .advantage-card::before,
  .services-advantage-card::before,
  .blog-card::before {
    display: none;
  }
  .product-card,
  .achievement-card,
  .task-card,
  .advantage-card,
  .services-advantage-card,
  .blog-card {
    border: 2px solid rgba(89, 194, 255, 0.35);
  }
}

