  color: rgba(255, 255, 255, 0.4);
  font-size: 18px;
}

.carousel-controls {
  display: flex;
  gap: 10px;
  flex-shrink: 0;
  margin-top: 0;
}

.carousel-btn {
  width: 45px;
  height: 45px;
  padding: 0;
  border-radius: 50%;
  background: transparent;
  border: 1px solid #59c2ff;
  color: #59c2ff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.carousel-btn img {
  display: block;
  width: 45px;
  height: 45px;
  object-fit: contain;
}

.carousel-btn:hover {
  background: #59c2ff;
  color: white;
}

/* Achievements Section */
.achievements {
  background: #000000;
  padding: 80px 0;
  border-radius: 50px 50px 0 0;
  margin-bottom: 0;
}

.achievements-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}

.achievement-card {
  --mouse-x: 50%;
  --mouse-y: 50%;
  background: #141414;
  border-radius: 20px;
  padding: 18px;
  height: 270px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
  position: relative;
  overflow: visible;
}

.achievement-card::before {
  content: "";
  position: absolute;
  left: -2px;
  top: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  padding: 2px;
  box-sizing: border-box;
  background: radial-gradient(
    550px circle at var(--mouse-x) var(--mouse-y),
    #59C2FF,
    transparent 50%
  );
  border-radius: 22px;
  z-index: 0;
  pointer-events: none;
  transition: opacity 500ms;
  -webkit-mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  mask-composite: exclude;
}

.achievement-card:hover::before {
  opacity: 1;
}

.achievement-card > * {
  position: relative;
  z-index: 1;
}

.achievement-icon {
  width: 66px;
  height: 66px;
  background: #252525;
  border-radius: 8.8px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.achievement-icon-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 8px;
}

.achievement-card p {
  font-size: 20px;
  line-height: 1.3;
  color: white;
}

.achievement-card strong {
  font-weight: 800;
}

/* Request Form Section */
.request-form {
  background: #000000;
  padding: 80px 0;
  position: relative;
  overflow: hidden;
  border-radius: 0;
  margin-top: 0;
}

.form-tabs {
  display: flex;
  gap: 5px;
  margin-bottom: 40px;
  margin-top: 0;
}

.tab {
  background: #252525;
  border: 1px solid #ACACAC;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s;
  opacity: 0.4;
}

.tab.active {
  background: #252525;
  border-color: #ACACAC;
  opacity: 1;
}

.tab:hover {
  opacity: 1;
}

.tab:not(.active) {
  opacity: 0.4;
}

.form-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.form-description {
  font-size: 18px;
  line-height: 1.2;
  color: white;
  margin: 0;
  width: 446px;
  flex-shrink: 0;
}

.form-description strong {
  font-weight: 700;
}

.contact-form {
  width: 630px;
  flex-shrink: 0;
}

.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  margin-bottom: 30px;
}

.form-input {
  background: transparent;
  border: 1px solid #acacac;
  color: #acacac;
  padding: 19px 18px;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 400;
  height: 50px;
  width: 100%;
}

.form-input::placeholder {
  color: #acacac;
}

.form-input:focus {
  outline: none;
  border-color: #ffffff;
  color: #ffffff;
}

.form-checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;
}

.checkbox {
  width: 15px;
  height: 15px;
