}

.form-background {
  display: none;
}

/* Contacts Section */
.contacts {
  background: #000000;
  background-image: url('img/background_contact.png');
  background-position: right center;
  background-repeat: no-repeat;
  background-size: auto;
  padding: 80px 0;
