/* Решаемые задачи */
.offtest-tasks {
  padding: 140px 0 0 0;
  background: #141414;
}

.tasks-grid {
  display: grid;
  grid-template-columns: repeat(3, 407px);
  gap: 29px;
  margin-top: 40px;
}

.task-card {
  --mouse-x: 50%;
  --mouse-y: 50%;
  background: #141414;
  border-radius: 20px;
  padding: 20px;
  padding-bottom: 60px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  position: relative;
  overflow: visible;
  transition: height 0.3s ease;
  box-sizing: content-box;
}

.task-card::before {
  content: "";
  position: absolute;
  left: -2px;
  top: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  padding: 2px;
  box-sizing: border-box;
  background: radial-gradient(
    550px circle at var(--mouse-x) var(--mouse-y),
    #59C2FF,
    transparent 50%
  );
  border-radius: 22px;
  z-index: 0;
  pointer-events: none;
  transition: opacity 500ms;
  -webkit-mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  mask-composite: exclude;
}

.task-card:hover::before {
  opacity: 1;
}

.task-card > * {
  position: relative;
  z-index: 1;
}

.task-card.collapsed {
  height: 220px;
}

.task-card.expanded {
  height: auto;
  overflow: visible;
}

.task-icon {
  width: 67px;
  height: 67px;
  background: #252525;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 67px;
  min-height: 67px;
}

.task-icon img {
  width: 50px;
  height: 50px;
  object-fit: contain;
  display: block;
  max-width: 50px;
  max-height: 50px;
}

.task-title {
  font-size: 20px;
  font-weight: 700;
  font-family: 'Manrope', sans-serif;
  color: white;
  margin: 0;
}

.task-description {
  font-size: 18px;
  line-height: 1.5;
  color: #4B4B4B;
  flex: 1;
  margin: 0;
  transition: filter 0.3s ease, color 0.3s ease;
  overflow: hidden;
}

.task-card.collapsed .task-description {
  filter: blur(2px);
