/* Преимущества системы */
.offtest-advantages {
  margin-top: 140px;
  padding: 80px 0 0;
  background: #000000;
  border-radius: 50px 50px 0 0;
}

.advantages-grid {
  display: grid;
  grid-template-columns: repeat(3, 407px);
  gap: 29px;
  margin-top: 40px;
  margin-bottom: 50px;
}

.advantage-card {
  --mouse-x: 50%;
  --mouse-y: 50%;
  background: #1a1a1a;
  border-radius: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  height: 220px;
  position: relative;
  overflow: visible;
}

.advantage-card::before {
  content: "";
  position: absolute;
  left: -2px;
  top: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  padding: 2px;
  box-sizing: border-box;
  background: radial-gradient(
    550px circle at var(--mouse-x) var(--mouse-y),
    #59C2FF,
    transparent 50%
  );
  border-radius: 22px;
  z-index: 0;
  pointer-events: none;
  transition: opacity 500ms;
  -webkit-mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  mask-composite: exclude;
}

.advantage-card:hover::before {
  opacity: 1;
}

.advantage-card > * {
  position: relative;
  z-index: 1;
}

.advantage-icon {
  width: 67px;
  height: 67px;
  background: #2a2a2a;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 67px;
  min-height: 67px;
}

.advantage-icon img {
  width: 50px;
  height: 50px;
  object-fit: contain;
  display: block;
  max-width: 50px;
  max-height: 50px;
}

