}

.services-header .section-title {
  margin-bottom: 0;
  line-height: 1.2;
  display: flex;
  align-items: center;
}

.services-icon-wrapper {
  position: relative;
}

.services-icon {
  width: 67px;
  height: 67px;
  background: #252525;
  border-radius: 8.933px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.services-icon .icon-bg {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.services-icon .icon-image {
  width: 52.111px;
  height: 52.111px;
  object-fit: contain;
}

.services-intro {
  font-size: 18px;
  font-weight: 500;
  color: white;
  line-height: 1.3;
  margin-bottom: 30px;
  max-width: 780px;
}

.services-tabs {
  display: flex;
  gap: 0;
  margin-bottom: 30px;
  border-bottom: 1px solid transparent;
}

.service-tab {
  padding: 15px 40px;
  background: transparent;
  border: none;
  border-bottom: 4px solid transparent;
  color: white;
  font-size: 14px;
  font-weight: 800;
  font-family: 'Manrope', sans-serif;
  cursor: pointer;
  transition: all 0.3s;
  white-space: nowrap;
}

.service-tab.active {
  border-bottom-color: white;
  color: white;
}

.service-tab:not(.active) {
  font-weight: 500;
  color: white;
  opacity: 0.7;
}

.service-tab:hover {
  opacity: 1;
}

.services-content-box {
  background: #141414;
  border: 2px solid #59c2ff;
  border-radius: 20px;
  padding: 30px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  margin-bottom: 30px;
}

.services-content-box:not([id*="-content"]) {
  display: grid;
}

.services-content-left,
.services-content-right {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.services-content-right {
  justify-content: flex-start;
}

.services-content-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.services-content-title {
  font-size: 24px;
  font-weight: 800;
  color: white;
  font-family: 'Manrope', sans-serif;
  margin: 0;
}

.services-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.services-list li {
  font-size: 18px;
  font-weight: 400;
  color: white;
  line-height: 1.3;
  position: relative;
  padding-left: 20px;
  display: block;
}

.services-list li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 6px;
  width: 10px;
  height: 10px;
  background: #59c2ff;
  border-radius: 50%;
}

.services-list li strong {
  font-weight: 700;
}

.services-content-illustration {
  margin-top: 30px;
  width: 100%;
  flex: 1;
  display: flex;
  align-items: flex-start;
}

.services-illustration-image {
  width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}

.services-illustration-placeholder {
  width: 100%;
  min-height: 400px;
  background: #1a1a1a;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #343434;
  color: #5f5f5f;
  font-size: 16px;
  text-align: center;
  padding: 40px;
}

.services-divider {
  height: 1px;
  background: #343434;
  margin: 30px 0;
}

/* Services Workflow Section */
.services-workflow {
  padding: 80px 0;
  background: #000;
  border-radius: 50px 50px 0 0;
}

.services-workflow-steps {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 40px;
  position: relative;
}

.services-workflow-step {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  position: relative;
}

.services-workflow-icon-wrapper {
  display: flex;
  align-items: center;
  gap: 18px;
  position: relative;
  width: 100%;
}

.services-workflow-icon {
  width: 65px;
  height: 65px;
  background: #252525;
  border-radius: 8.663px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.services-workflow-icon img {
  width: 36px;
  height: 36px;
  object-fit: contain;
}

.services-workflow-title {
  font-size: 20px;
  font-weight: 700;
  color: white;
  font-family: 'Manrope', sans-serif;
  line-height: 1.2;
  margin: 0;
}

.services-workflow-description {
  font-size: 14px;
  font-weight: 300;
  color: white;
  font-family: 'Manrope', sans-serif;
  line-height: 1.2;
  margin: 0;
}

.services-workflow-description strong {
  font-weight: 700;
}

/* Services Advantages Section */
.services-advantages {
  padding: 80px 0;
  background: #000000;
  border-radius: 0 0 50px 50px;
}

.services-advantages-grid {
  display: grid;
  grid-template-columns: repeat(3, 407px);
  gap: 29px;
  margin-top: 40px;
  justify-content: center;
}

.services-advantage-card {
  --mouse-x: 50%;
  --mouse-y: 50%;
  background: #141414;
  border-radius: 20px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-height: 234px;
  position: relative;
  overflow: visible;
}

.services-advantage-card::before {
  content: "";
  position: absolute;
  left: -2px;
  top: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  padding: 2px;
  box-sizing: border-box;
  background: radial-gradient(
    550px circle at var(--mouse-x) var(--mouse-y),
    #59C2FF,
    transparent 50%
  );
  border-radius: 22px;
  z-index: 0;
  pointer-events: none;
  transition: opacity 500ms;
  -webkit-mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  mask-composite: exclude;
}

.services-advantage-card:hover::before {
  opacity: 1;
}

.services-advantage-card > * {
  position: relative;
  z-index: 1;
}

.services-advantage-icon {
  width: 67px;
  height: 67px;
  background: #252525;
  border-radius: 8.933px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.services-advantage-icon img {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

.services-advantage-text {
  font-size: 20px;
  font-weight: 300;
  color: white;
  line-height: 1.2;
  margin: 0;
}

.services-advantage-text strong {
  font-weight: 700;
}

/* Services Page Responsive */
@media (max-width: 768px) {
  .services-section {
    padding: 40px 0;
  }

  .services-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }

  .services-tabs {
    flex-wrap: wrap;
    gap: 10px;
  }

  .service-tab {
    padding: 10px 20px;
    font-size: 12px;
  }

  .services-content-box {
    grid-template-columns: 1fr;
    padding: 20px;
    gap: 30px;
  }

  .services-content-title {
    font-size: 20px;
  }

  .services-list {
    gap: 15px;
  }

  .services-list li {
    font-size: 16px;
  }

  .services-workflow-steps {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .services-advantages-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .services-advantage-card {
    min-height: auto;
  }

  .services-illustration-placeholder {
    min-height: 300px;
    padding: 20px;
    font-size: 14px;
  }
}

/* Blog Section */
/* Breadcrumbs на странице блога */
body:has(.blog-section) .breadcrumbs {
  background: #000000;
  background-color: #000000;
}

.blog-section {
  padding: 40px 0 100px;
  background: #000;
  min-height: calc(100vh - 200px);
  border-radius: 0 0 50px 50px;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  width: 100%;
}

.blog-card {
  --mouse-x: 50%;
  --mouse-y: 50%;
  background: #141414;
  border-radius: 20px;
  overflow: visible;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 483px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative;
}

.blog-card::before {
  content: "";
  position: absolute;
  left: -2px;
  top: -2px;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  padding: 2px;
  box-sizing: border-box;
  background: radial-gradient(
    550px circle at var(--mouse-x) var(--mouse-y),
    #59C2FF,
    transparent 50%
  );
  border-radius: 22px;
  z-index: 0;
  pointer-events: none;
  transition: opacity 500ms;
  -webkit-mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  -webkit-mask-composite: xor;
  mask: linear-gradient(#fff, #fff) padding-box, linear-gradient(#fff, #fff) content-box;
  mask-composite: exclude;
}

.blog-card:hover::before {
  opacity: 1;
}

.blog-card:hover {
  box-shadow: 0 10px 30px rgba(89, 194, 255, 0.2);
}

.blog-card > * {
  position: relative;
  z-index: 1;
}

.blog-card-image {
  width: 100%;
  height: 249px;
  overflow: hidden;
  background: #1a1a1a;
  padding: 12px;
  box-sizing: border-box;
  border-radius: 20px 20px 0 0;
}

.blog-card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.blog-card-content {
  padding: 20px 12px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap: 12px;
  box-sizing: border-box;
}

.blog-card-title {
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  color: white;
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.blog-card-description {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  color: white;
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  flex-grow: 1;
}

.blog-card-button {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  font-weight: 700;
  color: white;
  text-decoration: none;
  padding: 11px 20px;
  border-radius: 30px;
  transition: all 0.3s ease;
  margin-top: auto;
  width: fit-content;
}

.blog-card-button-primary {
  border: 1px solid white;
}

.blog-card-button-primary:hover {
  background: white;
  color: #000;
}

.blog-card-button-primary::after {
  content: '››';
  margin-left: 10px;
  font-size: 18px;
  display: inline-block;
}

.blog-card-button:not(.blog-card-button-primary) {
  padding: 11px 0;
  border: none;
}

.blog-card-button:not(.blog-card-button-primary):hover {
  color: #59c2ff;
}

.blog-card-button:not(.blog-card-button-primary)::after {
  content: '›';
  margin-left: 10px;
  font-size: 18px;
  transform: rotate(270deg);
  display: inline-block;
}

/* Blog Responsive */
@media (max-width: 1400px) {
  .blog-grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }

  .blog-card {
    width: 100%;
  }
}

@media (max-width: 1200px) {
  .blog-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  .blog-card {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .blog-section {
    padding: 40px 0 60px;
  }

  .blog-grid {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .blog-card {
    height: auto;
    min-height: 483px;
  }

  .blog-card-image {
    height: 200px;
  }

  .blog-card-title {
    font-size: 20px;
  }

  .blog-card-description {
    font-size: 14px;
  }
}
